import React from 'react'
import { StyleSheet, TouchableOpacity, Text, Dimensions, ActivityIndicator } from 'react-native'
import PropTypes from 'prop-types'
import { colors } from '../constants'

const { width } = Dimensions.get('window')

export class Button extends React.Component {
    // wyświetl loader przy isLoading = true
    renderLoader() {
        return this.props.isLoading ? (
            <ActivityIndicator
                animating={true}
                color={colors.white}
                size={20}
            />
        ) : null
    }

    // wyświetl tekst na przycisku jeśli isLoading = false
    renderText() {
        return !this.props.isLoading ? (
            <Text
                style={[
                    styles.text,
                    {
                        color: this.props.textColor
                    }
                ]}
            >
                {this.props.text}
            </Text>
        ) : null
    }

    render() {
        return (
            <TouchableOpacity
                {...this.props}
                style={[
                    styles.button,
                    {
                        backgroundColor: this.props.disabled && this.props.disabledColor ?
                            this.props.disabledColor :
                            this.props.color
                    },
                    this.props.buttonStyles
                ]}
            >
                {this.renderLoader()}
                {this.renderText()}
            </TouchableOpacity>
        )
    }
}

Button.propTypes = {
    color: PropTypes.string,
    isLoading: PropTypes.bool.isRequired,
    textColor: PropTypes.string.isRequired,
    disabledColor: PropTypes.string,
    text: PropTypes.string.isRequired,
    disabled: PropTypes.bool,
    buttonStyles: PropTypes.object
}

const styles = StyleSheet.create({
    button: {
        height: 60,
        width: width - 20,
        borderRadius: 10,
        justifyContent: 'center',
        margin: 5,
        borderWidth: 1,
        borderColor: colors.noticeText
    },
    text: {
        textAlign: 'center',
        fontSize: 18
    }
})
