import { colors } from './colors'

// przypisz wielkość BMI do koloru i tłumaczenia
export const getBmiDetails = bmi => {
    if (!bmi) {
        return ({
            color: colors.black,
            text: ''
        })
    }

    if (bmi < 16) {
        return ({
            color: '#cd0038',
            text: 'wygłodzenie'
        })
    }

    if (bmi < 17) {
        return ({
            color: '#cd380b',
            text: 'wychudzenie'
        })
    }

    if (bmi < 18.5) {
        return ({
            color: '#cd9f05',
            text: 'niedowaga'
        })
    }

    if (bmi < 25) {
        return ({
            color: '#00cd32',
            text: 'prawidłowe'
        })
    }

    if (bmi < 30) {
        return ({
            color: '#cdc600',
            text: 'nadwaga'
        })
    }

    if (bmi < 35) {
        return ({
            color: '#cd9f05',
            text: 'I stopień otyłości'
        })
    }

    if (bmi < 40) {
        return ({
            color: '#cd380b',
            text: 'II stopień otyłości'
        })
    }

    return ({
        color: '#cd0038',
        text: 'III stopień otyłości'
    })
}
